<template>
  <Plotly
    v-bind:data="data"
    :layout="layout"
    :config="config"
    :display-mode-bar="false"
  ></Plotly>
</template>

<script>
import { Plotly } from "vue-plotly";
export default {
  components: {
    Plotly,
  },
  props: ["data"],
  data() {
    return {
      config: { responsive: true },
      layout: {
        height: 1000,
        scene: {
          xaxis: { title: "Направление (x)" },
          yaxis: { title: "Скорость (y)" },
          zaxis: { title: "Pm (z)" },
        },
      },
    };
  },
};
</script>

