<template>
  <div class="settings">
    <form>
      <label class="block">Дата</label>
      <date-picker
        class="block"
        v-model="time"
        range
        valueType="format"
      ></date-picker>
    </form>
    <form>
      <label class="block">Агрегирование</label>
      <select
        v-model="interval"
        class="block"
        style="height: 34px; font-size: 14px"
      >
        <option value="1hour">Час</option>
        <option value="6hour">6 часов</option>
        <option value="12hour">12 часов</option>
        <option value="1day">День</option>
      </select>
    </form>
    <form style="display: none">
      <label class="block">Тип графика</label>
      <select
        v-model="type"
        class="block"
        style="height: 34px; font-size: 14px"
      >
        <option value="mesh3d">Mesh</option>
        <option value="scatter3d">Scatter</option>
        <option value="surface">Surface</option>
      </select>
    </form>
    <button @click="Refresh" style="height: 34px">Обновить</button>
  </div>
</template>

<script>
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/index.css";
export default {
  components: { DatePicker },
  data() {
    return {
      time: ["2021-03-01", "2021-03-31"],
      interval: "6hour",
      type: "scatter3d",
    };
  },
  methods: {
    Refresh() {
      this.$emit("ref", this.time, this.interval, this.type);
    },
  },
};
</script>

