
async function DownloadPm(time, interval) {
    const URL = `https://gis.krasn.ru/sc/api/1.0/projects/9/aggvalues?&sites=3837&key=654hblgm9gl8367h&time_begin=${time[0]} 00:00:00&time_end=${time[1]} 00:00:00&time_interval=${interval}&indicators=348&limit=30000'`;
    const responce = fetch(URL);
    const result = responce.then(e => XML2JSON(e));
    return result;
}
async function DownloadWind(time, interval) {
    const URL = `https://gis.krasn.ru/sc/api/1.0/projects/10/aggvalues?&sites=4226&key=654hblgm9gl8367h&time_begin=${time[0]} 00:00:00&time_end=${time[1]} 00:00:00&time_interval=${interval}&indicators=101,102&limit=30000'`;
    const responce = fetch(URL);
    const result = responce.then(e => XML2JSON(e));
    return result;
}


