function DirectionToNumber(value) {
    if (value >= 0 && value < 11.25)
        return 0
    if (value >= 11.25 && value < 33.75)
        return 1
    if (value >= 33.75 && value < 56.25)
        return 2
    if (value >= 56.25 && value < 78.75)
        return 3
    if (value >= 78.75 && value < 101.25)
        return 4
    if (value >= 101.25 && value < 123.75)
        return 5
    if (value >= 123.75 && value < 146.25)
        return 6
    if (value >= 146.25 && value < 168.75)
        return 7
    if (value >= 168.75 && value < 191.25)
        return 8
    if (value >= 191.25 && value < 213.75)
        return 9
    if (value >= 213.75 && value < 236.25)
        return 10
    if (value >= 236.25 && value < 258.75)
        return 11
    if (value >= 258.75 && value < 281.25)
        return 12
    if (value >= 281.25 && value < 303.75)
        return 13
    if (value >= 303.75 && value < 326.25)
        return 14
    if (value >= 326.25 && value < 348.75)
        return 15
    if (value >= 348.75)
        return 0
}


async function XML2JSON(e) {
    const r = e.text().then(e => {
        const result = [];
        const dom = new DOMParser();
        const xml = dom.parseFromString(e, "text/xml");
        const aggvalues = xml.getElementsByTagName("aggvalue");
        let index = 0;
        aggvalues.forEach((element) => {
            const indicator = element.getAttribute("indicator")
            const time = element.getAttribute("time").substring(0, element.getAttribute("time").length - 3);
            let value = "NONE";

            if (indicator == 101) {
                value = DirectionToNumber(Number(element.getElementsByTagName("avg")[0].innerHTML));
                index++;
            }
            if (indicator == 348) {
                value = Number((element.getElementsByTagName("avg")[0].innerHTML) * 1000).toFixed(4);
            }
            if (indicator == 102) {
                value = Number(element.getElementsByTagName("avg")[0].innerHTML).toFixed(1);

            }
            result.push({ indicator: indicator, time: time, value: value, index: index - 1 })
        })
        return result;
    })
    return r;
}

function GetItem(data, indicator) {
    const result = data.filter(e => { if (e.indicator == indicator) return e });
    return result
}

function GetValues(data) {
    try {
        const times = data.map(e => { return e.time });
        const values = data.map(e => { return e.value });
        return { times, values };
    }
    catch
    {
        alert("Выберите другую дату или другое агрегирование")
        return []
    }

}

function DataForSurface(x, y, z) {
    const direction = [
        "С",
        "ССВ",
        "СВ",
        "ВСВ",
        "В",
        "ВЮВ",
        "ЮВ",
        "ЮЮВ",
        "Ю",
        "ЮЮЗ",
        "ЮЗ",
        "ЗЮЗ",
        "З",
        "ЗСЗ",
        "СЗ",
        "ССЗ",
    ];

    let result = [];
    for (let i = 0; i < 12; i++) {
        let zero = [];
        for (let j = 0; j < direction.length; j++) {
            zero.push(0);
        }
        result.push(zero);
    }
    console.log(result);
    console.log(x, y, z);
    x.forEach((e, i) => {
        const index = direction.findIndex(dir => e == dir);
        if (result[Math.round(Number(y[i]))][index] < Number(z[i])) {
            result[Math.round(Number(y[i]))][index] = z[i];
        }

    })
    return result
}
export { DownloadPm, DownloadWind, GetItem, GetValues, DataForSurface }